import Vue from 'vue'
import Router from 'vue-router'
import WeatherPage from '@/components/WeatherPage'
import HomePage from "../components/HomePage";

import { Button, message, Layout, Menu, Icon, Form} from 'ant-design-vue';

Vue.use(Router)

Vue.use(Button)
Vue.use(Layout)
Vue.use(Menu)
Vue.use(Icon)
Vue.use(Form)


export default new Router({
  routes: [
    {
      path: '/WeatherTool',
      name: 'WeatherPage',
      component: WeatherPage
    },
    {
      path: '/',
      name: 'HomePage',
      component: HomePage
    },
    {
      path: '/about',
      name: 'HomePage',
      component: HomePage
    }
  ]
})



