<template>
  <div id="app">

    <header
      style="color: #fff;
      height: 200px;
      background-image: linear-gradient(0deg, #001529, #159957)"
    >
    <img src="../static/xtao_logo.png" style="height: 200px; width: 200px">
    </header>
    <a-layout>

      <a-layout-sider
        style="background-color: #001529">

        <a-menu
          theme="dark"
          :default-selected-keys="['/']"
          mode="inline"
          @click="menuClick"
        >

          <a-menu-item key="/" >
            <a-icon type="home"/>主页
          </a-menu-item>


          <a-sub-menu
            theme="dark"
            mode="inline"
            @click="menuClick"
          >
            <span slot="title"><a-icon type="tool" /><span>软件</span></span>

            <a-menu-item key="/WeatherTool" >
              <a-icon type="cloud" />天气小助手
            </a-menu-item>

          </a-sub-menu>


<!--          <a-menu-item key="3">-->
<!--            <a-icon type="file-text"/> 资料-->
<!--          </a-menu-item>-->

          <a-menu-item key="/about">
            <a-icon type="message"/> 关于
          </a-menu-item>
        </a-menu>
      </a-layout-sider>

      <a-layout :style="{background:  'white'}">
        <a-layout-content
          :style="{ margin: '24px 16px', padding: '24px', minHeight: '280px' }"
        >
          <router-view/>
        </a-layout-content>

        <a-layout-footer style="text-align: center; background-color: white; color: black">
          xtao web page ©2018 Created by xtao@Excellent Studio
        </a-layout-footer>
      </a-layout>

    </a-layout>

  </div>

</template>

<script>
  export default {
    name: 'App',
    data() {
      return {
        collapsed: false,
      };
    },
    methods: {
      menuClick(e) {
        // 获取到当前的key,并且跳转
        this.$router.push({
          path: e.key
        })
      },
    },
  }
</script>

<style>
  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    margin-top: 0;
    margin-bottom: 0;
  }

</style>



