        <a-col :span="18">
          <a-row>
            <a-col span="16">
              <a style="font-size: 30pt; color: #159957;">天气小助手</a>
            </a-col>

            <a-col span="4">
            <a-button class="download_btn" @click="open_source_web('https://github.com/Taotaoxu')">查看源码</a-button>
            </a-col>

            <a-col span="4">
              <a-button class="download_btn" @click="download_func('WeatherTool/天气小助手Installer.msi', '天气小助手Installer.msi')">下载</a-button>
            </a-col>
          </a-row>

          <a-row>

            <a-col :span="4" style="text-align: right">
              <a-row>
                <a class="soft_info_txt">语言:</a>
              </a-row>

              <a-row>
                <a class="soft_info_txt">版本号:</a>
              </a-row>

              <a-row>
                <a class="soft_info_txt">发布时间:</a>
              </a-row>

              <a-row>
                <a class="soft_info_txt">运行环境:</a>
              </a-row>

              <a-row>
                <a class="soft_info_txt">功能描述:</a>
              </a-row>

            </a-col>

            <a-col :span="1">

            </a-col>

            <a-col :span="19" style="text-align: left">

              <a-row>
                <a class="soft_info_txt">C#</a>
              </a-row>

              <a-row>
                <a class="soft_info_txt">1.0.0</a>
              </a-row>

              <a-row>
                <a class="soft_info_txt">2021-08-09</a>
              </a-row>

              <a-row>
                <a class="soft_info_txt">Windows 10, .Net Framework 4.6.1</a>
              </a-row>

              <a-row>
                <a class="soft_info_txt">一款用于Windows（7，8，10）平台的桌面工具软件，用于实时获取近期天气情况，并在桌面底层可视化展示。</a>
              </a-row>
            </a-col>
