    <a-carousel arrows autoplay>
      <div
        slot="prevArrow"
        slot-scope="props"
        class="custom-slick-arrow"
        style="left: 10px;zIndex: 1;"
      >
        <a-icon type="left-circle" />
      </div>
      <div slot="nextArrow" slot-scope="props" class="custom-slick-arrow" style="right: 10px">
        <a-icon type="right-circle" />
      </div>

      <div class="div_c"><img src="/static/WeatherTool/01-1.jpg"></div>
      <div class="div_c"><img src="/static/WeatherTool/01-2.jpg"></div>
      <div class="div_c"><img src="/static/WeatherTool/01-3.jpg"></div>
      <div class="div_c"><img src="/static/WeatherTool/01-4.jpg"></div>
