      <li class="soft_feature_txt">自定义国内所有城市</li>
      <li class="soft_feature_txt">定时更新天气状态，并可视化展示</li>
      <li class="soft_feature_txt">动态显示24小时/7日内的天气信息</li>
      <li class="soft_feature_txt">在桌面壁纸与桌面图标之间的层上显示</li>
    </ul>

      <br>

      <h3 style="font-size: 20pt; color: #159957;">注意事项</h3>
      <ul>
        <li class="soft_feature_txt">
          <strong>代码开源，绝对无毒，请先设置白名单后，再点击运行。</strong>
          由于软件显示于桌面壁纸与桌面图标之间的层，需要调用系统底层API，从而导致“Windows安全中心”和“360安全管家”等软件误判为病毒，请将其设置为白名单后，在启动运行。
        </li>
        <li class="soft_feature_txt">白名单设置方法：
          <a ref="https://support.microsoft.com/zh-cn/windows/%E5%B0%86%E6%8E%92%E9%99%A4%E9%A1%B9%E6%B7%BB%E5%8A%A0%E5%88%B0-windows-%E5%AE%89%E5%85%A8%E4%B8%AD%E5%BF%83-811816c0-4dfd-af4a-47e4-c301afe13b26">Windows安全中心白名单设置方法</a>
          ，
          <a ref="https://jingyan.baidu.com/article/fb48e8be6b1ccd2e632e1432.html">360安全管家白名单设置方法</a>
        </li>
        <li class="soft_feature_txt">以管理员身份运行</li>
      </ul>

    </div>

  </div>
</template>

<script>
  export default {
    name: 'WeatherPage',
    data() {
      return {
        msg: 'Welcome to Your Vue.js App',
      }
    },
    methods: {
      download_func(file_path, file_name) {
        let a = document.createElement('a');
        a.href = '/static/' + file_path;
        //路径中'/'为根目录，即index.html所在的目录
        a.download = file_name;
        a.click();
      },

      open_source_web(github_path){
        window.open(github_path, '_blank');
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  /* For demo */
  .ant-carousel >>> .slick-slide {
    text-align: center;
    /*height: 400px;*/
    /*line-height: 400px;*/
    background: #ffffffff;
    overflow: hidden;
  }

  .ant-carousel >>> .custom-slick-arrow {
    width: 50px;
    height: 50px;
    font-size: 50px;
    color: #2c3e50;
    background-color: rgba(31, 45, 61, 0);
    opacity: 0.9;
  }
  .ant-carousel >>> .custom-slick-arrow:before {
    display: none;
  }
  .ant-carousel >>> .custom-slick-arrow:hover {
    opacity: 0.5;
  }

  .ant-carousel >>> .slick-slide h3 {
    color: #fff;
  }


  .div_c{
    height:400px;
    display: table-cell;
    vertical-align: middle;
  }

  .div_c img{
    max-width: 100%;
    max-height: 100%;
    display: block;
    margin: auto;
  }

  .soft_info_txt{
    font-size: 18pt;
    color: #001529
  }

  .soft_feature_txt{
    font-size: 15pt;
    color: #001529
  }

  .download_btn{
    font-size: 15pt;
    color: #159957;
    height: 60px;
    width: 120px;
  }

</style>



